body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-thumb{
  border-radius: 30px;
  background: -webkit-gradient(linear,left top,left bottom, from(#ff8a00),to(#da1b60));
  box-shadow: 2px 2px 2px rgba(255, 255, 255, 0.25), inset -2px -2px -2px rgba(0, 0, 0, 0.25);
}
Container { padding-top: 40px; }
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.homeImg{
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(/landing-page/static/media/1.17c297e8.jpeg) no-repeat center center/cover;
    height: 100vh;
}
.bg{
    background-color: #15102c;
    color: #fa741b;
}

.form{
    background: rgba(168, 167, 167,0.4);
    background-size: 40px 60px;
    width: 100% ;
}
.margin{
    margin: 5% 0;
}
.parallax{
    background:rgba(0, 0, 0, 0.6) url(/landing-page/static/media/2.996ab4fd.jpeg) no-repeat fixed center/cover;
    background-blend-mode: darken ;
}

.parallax-inner{
    padding-top: 10%;
    padding-bottom: 10%;
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
    color: #ffffff;
    font-size: 1.2rem;
}
h2{
    text-align: center;
    padding: 25px ;
    margin-bottom: 0;
    color: #fa741b;
}
h5{
    color: #fa741b;
}
.cardX{
    width: 70%;
    min-height: 300px;
    margin:auto;
    border-radius: 20px;
    background-color: #15102e;
    border: solid 2px #fa741b;
    overflow: hidden;
}

.card-title{
    margin-left: 1rem;
}

.card-text{
    color: white;
    margin: 1rem;
}

.row{
    margin-right: 0;
    margin-left: 0;
}

@media only screen and (max-width:300px){
    .card p{
        display: none;
    }
    .card{
        padding: 70px 0;
    }

}

.back-ground{
    background:#15102ece url('https://images.unsplash.com/photo-1599256872237-5dcc0fbe9668?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80') no-repeat center center/cover;
    height: 200px;
    width: 100%;
    background-blend-mode:darken ;
    border-top: 4px solid #fa741b ;
    border-bottom: 4px solid #fa741b ;
}
/*color code :#15102e purple
color code : #fa741b orange
color code : #fbfffd white
*/

.Navbar-color {
  background-color: #15102e;
}
.Navbar-text-color{
  color: #fbfffd;
}
.NavbarItem {
  color: white;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 1.5rem;
}
.NavbarItem:hover  {
  color: #fa741b;
}
h3,h6{
  text-align:right;
  color:#fa741b;
}
Link{
  text-decoration: none;
}
.widgets{
    position: fixed;
    bottom: 40%;
    right: 0px;
    z-index: 100;
}
.backgroundimg{
  background: url(/landing-page/static/media/background.87872d50.jpg) no-repeat center center fixed;
  height:35vh;
  width:auto;
}
.check{
  margin:25%;
}
.line{
  padding-left:20%;
  padding-right:20%;
}
.prod{
  width:42vw;
}
@media only screen
   and (max-width : 490px) {
.prod{
  display:flex;
  flex-wrap: wrap;
  width:90%;
  margin-left:5vw;
}
.check{
  margin:5%;
  margin-left:20%;
}
}

@media only screen
   and (max-width :780px) {
.prod{
  width:70%;
  margin-left:15vw;
}
.check{
  margin:10%;
  margin-left:25%;
}
}
/* check 1024 */

@media only screen
   and (max-width :1024px) {
.prod{
  width:65%;
  margin-left:18vw;
}
.check{
  margin:5%;
  margin-left:25%;
}
}

